
html,
body {
  margin: 0;
  padding: 0;
}
body.disable-scroll {
  overflow: hidden;
  min-height: 100%;
  max-height: 100vh;
  padding-bottom: 1px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.drawer {
  background-color: #313241;
  overflow-y: auto;
  overflow-x: none;
  padding-bottom: 60px;
  position: fixed;
  z-index: 51;
}
#content-overlay {
  background-color: rgba(0,0,0,0);
  pointer-events: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: background-color 0.6s;
  z-index: 50;
}
#content-overlay.visible {
  background-color: rgba(0,0,0,0.85);
  pointer-events: all;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
