html {
  background-color: #38394a;
}
.main-page {
  padding-top: 60px;
}
.main-page .col {
  box-sizing: border-box;
  padding: 0;
  top: 60px;
}
.main-page .col .container {
  padding: 20px;
}
.main-page .col .container h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.main-page .col#col-main {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin: 0 auto 200px auto;
  max-width: 780px;
}
@media (max-width: 800px) {
.main-page .col#col-main {
    max-width: 100%;
    margin: 0 10px;
}
}
.content-block {
  border-radius: 2px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
#app.authenticating .main-page .col {
  top: 0;
}
#action-panel {
  transition: all 0.3s linear;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  left: 50%;
  margin-left: -400px;
  height: 140px;
  width: 800px;
  z-index: 50;
}
@media (max-width: 800px) {
#action-panel {
    left: 0;
    margin-left: 0;
    width: 100%;
}
}
#action-panel.segment-view {
  bottom: 20px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  margin-left: -370px;
  width: 740px;
  z-index: 52;
}
#action-panel.segment-view .video-wrapper {
  margin-left: -370px;
}
@media (max-width: 800px) {
#action-panel.segment-view {
    left: 0;
    margin-left: 10px;
    width: calc(100% - 20px);
}
}


