html {
  background-color: #38394a;
}
.main-page {
  padding-top: 60px;
}
.main-page .col {
  box-sizing: border-box;
  padding: 0;
  top: 60px;
}
.main-page .col .container {
  padding: 20px;
}
.main-page .col .container h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.main-page .col#col-main {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  margin: 0 auto 200px auto;
  max-width: 780px;
}
@media (max-width: 800px) {
.main-page .col#col-main {
    max-width: 100%;
    margin: 0 10px;
}
}
.content-block {
  border-radius: 2px;
  padding: 20px;
  margin: 20px 0 0 0;
}
.content-block.white-block {
  background-color: #fff;
}
#app.authenticating .main-page .col {
  top: 0;
}
.rendered-markdown h1 {
  margin: 0;
  padding: 0;
  color: #444;
  margin-bottom: 5px;
}
.rendered-markdown h1,
.rendered-markdown h2,
.rendered-markdown h3,
.rendered-markdown h4,
.rendered-markdown h5,
.rendered-markdown p,
.rendered-markdown a,
.rendered-markdown li {
  color: #444;
}
.rendered-markdown img {
  max-width: 100%;
}
.fourcorners-submission {
  border-radius: 6px;
  background-color: #0078e7;
  box-sizing: border-box;
  padding: 15px;
  width: 100%;
}
.fourcorners-submission label {
  color: #fff;
}
.fourcorners-submission p {
  margin: 0;
  padding: 0;
  color: #fff;
}
.fourcorners-submission textarea {
  border-radius: 6px;
  border: none;
  box-shadow: none;
  box-sizing: border-box;
  line-height: 40px;
  margin: 10px 0;
  padding: 0 10px;
  outline: 0;
  resize: none;
  width: 100%;
}
.fourcorners-submission button {
  margin-top: 10px;
}


