
html,
body {
  margin: 0;
  padding: 0;
}
body.disable-scroll {
  overflow: hidden;
  min-height: 100%;
  max-height: 100vh;
  padding-bottom: 1px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.clearfix {
  clear: both;
  float: none;
}
.fa-icon {
  width: auto;
  height: 1em;
}
.background-white {
  background-color: #fff !important;
}
.text-white {
  color: #fff;
}
.drawer {
  background-color: #313241;
  overflow-y: auto;
  overflow-x: none;
  padding-bottom: 60px;
  position: fixed;
  z-index: 51;
}
#content-overlay {
  background-color: rgba(0,0,0,0);
  pointer-events: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: background-color 0.6s;
  z-index: 50;
}
#content-overlay.visible {
  background-color: rgba(0,0,0,0.85);
  pointer-events: all;
}
.pure-button.full-width {
  display: block;
  margin-bottom: 10px;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-to,
.fade-leave {
  opacity: 1;
}
.message {
  max-height: 98px;
  margin: 0 15px 0 15px;
  padding: 0 10px 50px 10px;
  padding-left: 50px;
  position: relative;
}
.message img.profile-image {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  position: absolute;
  top: 5px;
  left: 0;
}
.message p.message-content {
  margin: 0;
  padding: 0;
  color: #444;
}
.message p.message-content a,
.message p.message-content a:active {
  color: #444;
}
.message a.author-label {
  color: #46475d;
  font-weight: bold;
  text-decoration: none;
  font-size: 0.9em;
}
.message .message--footer {
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 36px;
  left: 40px;
  top: auto;
  overflow: hidden;
  position: absolute;
}
.message .message--footer ul.tweet-actions {
  margin: 0;
  padding: 0;
  list-style: none;
}
.message .message--footer ul.tweet-actions li {
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  float: left;
  font-size: 1em;
  line-height: 36px;
  margin: 0 10px;
  max-width: 15%;
}
.message .message--footer ul.tweet-actions li a {
  color: #ccc;
  text-decoration: none;
}
.message .message--footer ul.tweet-actions li a:hover {
  color: #0078e7;
}
.message .message--footer ul.tweet-actions li.message-timestamp {
  max-width: 55%;
}
.message .message--footer ul.tweet-actions li.message-timestamp a {
  font-size: 0.8 !important;
}


